import { CButton, CFormGroup, CInput, CLabel, CRow, CCol, CTextarea, CForm, CDataTable, CModal, CModalHeader, CModalBody, CModalFooter, CBadge } from '@coreui/react'
import {React, useState} from 'react'
import { useDispatch } from 'react-redux'
import Select from 'react-select'

const AjuSKA = (props) =>{
    const [page, setPage] = useState(1)
    const [pageBarang, setPageBarang] = useState(1)
    const [modal, setModal] = useState(false)
    const dispatch = useDispatch()
    return(
        <>
        <div className="d-flex flex-row justify-content-start mb-2">
            <div className="mr-4">
            <CButton onClick={()=>{
                props.changePage(0)
                dispatch({type:'set',tabtrue:true})                
            }} style={{borderRadius:'12px'}} className="btn btn-sm btn-light mr-auto"><i class="fas fa-chevron-left"></i> Kembali</CButton>                
            </div>
            <div className="d-flex align-items-center">
                <b>Pengajuan Surat Keterangan Asal Kemendag</b>
            </div>            
        </div>
        <hr/>     
        </>
    )
}

export default AjuSKA