import {React, useState} from 'react'
import ListLayanan from './listlayanan'
import AjuBarantan from './ajubarantan'
import AjuBKIPM from './ajubkipm'
import AjuBC from './ajubc'
import AjuSKA from './ajuska'
import { CDataTable, CButton} from '@coreui/react'

const DataLayanan = () =>{
    const [page, setPage] = useState(0)
    return(
        <>
        {
            page === 0 ?
            <ListLayanan changePage={setPage}/>  
            :
            page === 1 ?
            <AjuBC changePage={setPage}/> 
            :
            page === 2 ?
            <AjuBarantan changePage={setPage}/>
            :
            page === 3 ?
            <AjuBKIPM changePage={setPage}/>
            :
            page === 4 ?
            <AjuSKA changePage={setPage}/>
            :
            <>
            </>         
        }
        </>
    )
}

export default DataLayanan