import { CFormGroup, CLabel, CModal, CModalBody, CModalHeader, CRow, CCol, CModalFooter, CButton, CTextarea, CInput} from '@coreui/react'
import {React, useEffect, useState} from 'react'
import { useDispatch, useSelector } from 'react-redux'
import Select from 'react-select'
import {DataUmum, DataHewan, DataTumbuhan, DataIkan, BeaKeluar} from './bridge'

const ModalBarang = (props) =>{
    const dispatch = useDispatch()
    const pilihan = useSelector((state)=>state.pilihan)
    const [tab, setTab] = useState(<DataUmum/>)
    const [value, setValue] = useState(1)
    const [modal, setModal] = useState(false)
    useEffect(()=>{
        setModal(props.modal)
    },[props.modal])
    const arrKategoriBarang = {
        1:[
            {
                value:2,
                label:"Data Hewan Non Ikan",
                object:<DataHewan/>
            }            
        ],
        2:[
            {
                value:3,
                label:"Data Tumbuhan [Barantan]",
                object:<DataTumbuhan/>
            },
            {
                value:2,
                label:"Data Hewan [Barantan]",
                object:<DataHewan/>
            }                                   
        ],
        3:[
            {
                value:4,
                label:"Data Ikan",
                object:<DataIkan/>
            }            
        ],
        0:[]                      
    }
    return(
        <CModal
        show={modal}
        size="xl"
        onClose={()=>{
            setModal(false)
            props.callback(false)
            dispatch({type:'set',valBarang:{['hscode']:null,['barantanFlag']:false,['barantanDisabled']:false},['pilihan']:0})
        }}
        >
            <CModalHeader closeButton><h5>Tambah Barang</h5></CModalHeader>
            <CModalBody>
                <div className="d-flex flex-row justify-content-end mb-2">
                    <div className="mr-1">
                        <input type="checkbox"></input>
                    </div>
                    Termasuk Karantina Barantan
                </div>                  
                <div className="card px-3 py-3">
                    <CRow>
                        <CCol md="6">
                            <CFormGroup>
                                <CLabel>HS / Seri</CLabel>
                                <Select/>
                            </CFormGroup>
                        </CCol>
                        <CCol md="6">
                            <CFormGroup>
                                <CLabel>Deskripsi HS</CLabel>
                                <CInput disabled/>
                            </CFormGroup>
                        </CCol>                        
                    </CRow>
                    <CRow>
                        <CCol md="12">
                            <CFormGroup>
                                <CLabel>Uraian</CLabel>
                                <CInput disabled/>
                            </CFormGroup>
                        </CCol>
                    </CRow>
                    <CRow>
                        <CCol md="12">
                            <CFormGroup>
                                <CLabel>Uraian Masterlist</CLabel>
                                <CInput disabled/>
                            </CFormGroup>
                        </CCol>
                    </CRow>
                    <CRow>
                        <CCol md="12">
                            <CFormGroup>
                                <CLabel>Uraian Perizinan</CLabel>
                                <CInput disabled/>
                            </CFormGroup>
                        </CCol>
                    </CRow>  
                    <CRow>
                        <CCol md="3">
                            <CFormGroup>
                                <CLabel>Merk</CLabel>
                                <CInput disabled/>
                            </CFormGroup>
                        </CCol>
                        <CCol md="3">
                            <CFormGroup>
                                <CLabel>Tipe</CLabel>
                                <CInput disabled/>
                            </CFormGroup>
                        </CCol>
                        <CCol md="3">
                            <CFormGroup>
                                <CLabel>Ukuran</CLabel>
                                <CInput disabled/>
                            </CFormGroup>
                        </CCol>
                        <CCol md="3">
                            <CFormGroup>
                                <CLabel>Spesifikasi Lain</CLabel>
                                <CInput disabled/>
                            </CFormGroup>
                        </CCol>                                                                        
                    </CRow> 
                    <CRow>
                        <CCol md="3">
                            <CFormGroup>
                                <CLabel>Kode Barang</CLabel>
                                <CInput disabled/>
                            </CFormGroup>
                        </CCol>
                        <CCol md="3">
                            <CFormGroup>
                                <CLabel>Kondisi Barang</CLabel>
                                <CInput disabled/>
                            </CFormGroup>
                        </CCol>
                        <CCol md="3">
                            <CFormGroup>
                                <CLabel>Negara Asal</CLabel>
                                <Select isDisabled/>
                            </CFormGroup>
                        </CCol>
                        <CCol md="3">
                            <CFormGroup>
                                <CLabel>Spesifikasi Lain</CLabel>
                                <Select isDisabled/>
                            </CFormGroup>
                        </CCol>                                                                        
                    </CRow>  
                    <CRow>
                        <CCol md="5">
                            <CFormGroup>
                                <CLabel>Netto</CLabel>
                                <CInput disabled/>
                            </CFormGroup>
                        </CCol>
                        <CCol md="1">
                            <CFormGroup>
                                <CLabel>Satuan</CLabel>
                                <CInput placeholder="KGM" disabled/>
                            </CFormGroup>
                        </CCol>
                        <CCol md="5">
                            <CFormGroup>
                                <CLabel>Bruto</CLabel>
                                <CInput disabled/>
                            </CFormGroup>
                        </CCol>
                        <CCol md="1">
                            <CFormGroup>
                                <CLabel>Satuan</CLabel>
                                <CInput placeholder="KGM" disabled/>
                            </CFormGroup>
                        </CCol>                                                                        
                    </CRow> 
                    <hr/>
                    <CRow>
                        <CCol md="3">
                            <CFormGroup>
                                <CLabel>Jumlah Satuan</CLabel>
                                <CInput disabled/>
                            </CFormGroup>
                        </CCol>
                        <CCol md="3">
                            <CFormGroup>
                                <CLabel>Kode Satuan</CLabel>
                                <Select isDisabled/>
                            </CFormGroup>
                        </CCol>
                        <CCol md="3">
                            <CFormGroup>
                                <CLabel>Jumlah Kemasan</CLabel>
                                <CInput disabled/>
                            </CFormGroup>
                        </CCol>
                        <CCol md="3">
                            <CFormGroup>
                                <CLabel>Kode Kemasan</CLabel>
                                <Select isDisabled/>
                            </CFormGroup>
                        </CCol>                                                                        
                    </CRow>                                                                                                                     
                </div>
            </CModalBody>
            <CModalFooter>
                <div className="d-flex flex-row justify-content-end">
                    <div className="mr-2">
                        <CButton onClick={()=>{
                            setModal(false)
                            props.callback(false)
                        }} className="btn btn-success">Simpan</CButton>
                    </div>
                    <CButton onClick={()=>{
                        setModal(false)
                        props.callback(false)
                    }} className="btn btn-danger">Batal</CButton>
                </div>
            </CModalFooter>
        </CModal>
    )
}

export default ModalBarang